import React from "react"
import { Container, Badge } from "react-bootstrap"
import BaseExhibitionIdent from "../../../../../components/static/BaseExhibitionIdentities"
import QuestionTwoIdent from "../../../../../components/shared/ExhibitionQuestions/questions/B_question"

const questionTwoPage = () => {

return (
    <BaseExhibitionIdent>
     <Container fluid className="mt-5 p-5">

{/*Intro text*/}
        <div className="d-grid gap-2">
          <Badge className="badgeIdentities rounded-pill"><h1>ReThinking IDENTITIES</h1></Badge>
        </div>

        <div className="identitiesQuestions">
            <h4>What is your relationship to conflicts? <br />
            Find out by answering the quiz questions</h4>
        </div>


{/*Actual Form answers - radio checks; followed by button to next page*/}
        <div className="identitiesAnswers">
            <QuestionTwoIdent></QuestionTwoIdent>
        </div>
        
     </Container>
    </BaseExhibitionIdent>
    )
  }

export default questionTwoPage;