import React, {useState, useEffect} from "react"
import { Button, Form } from "react-bootstrap"


const QuestionTwoIdent = () => {

    const [answerTwo, setAnswerTwo] = useState() 

    useEffect(() => {
        localStorage.setItem('answerTwo', JSON.stringify(answerTwo));
      }, [answerTwo]);
    

// these set the answer, once clicked, within the Form - the recorded value is stored as "answerOne"
    const AnswersTwoIdent = [
        "Answer 1. test for 2.", 
        "Answer 2. test for 2", 
        "Answer 3. test for 2"
    ]
    const onOptionChange = e => {
        setAnswerTwo(e.target.value) 
    }

return (
<React.Fragment>

<h1 className="mt-5">Question 2: new test question what needs to be improved here?</h1>

        <Form onSubmit={(e) => e.preventDefault()}>
            {AnswersTwoIdent.map(possAnswTwo => (
          <Form.Check
            type="radio"
            value={possAnswTwo}
            label={possAnswTwo}
            checked={possAnswTwo === answerTwo }
            onChange={onOptionChange}
            ></Form.Check>
        ))}
        </Form>

            <div className="d-grid mt-5">
                <Button className="btn-primary rounded-pill m-3 mt-0" href="exhibitions/identities/questions/3rdQuestionIdent">
                    <h1 style={{color: "white"}}>Next Page</h1>
                </Button>
            </div>

</React.Fragment>
    )
  }
  
export default QuestionTwoIdent

